import {Droppable, Draggable} from '../../../scripts/vendor/draggable';
import SoundFx from '../../SoundFx';

export default function BasicsSection() {
  const containers = document.querySelectorAll('.CubesFrame--idBasics');

  const draggable = new Droppable(containers, {
    draggable: '.draggable-source',
    droppable: '.Cube--typeDroppable',
    appendTo: '.CubesFrame--idBasics',
    classes: {
      body: 'draggable-container--is-dragging',
    },
    plugins: [
      Draggable.Plugins.Accessibility,
      Draggable.Plugins.Mirror,
      Draggable.Behaviour.Snappable,
    ],
  });

  // --- Drag states --- //
  const state = {
    justLifted: false,
    snappedIn: false,
    snappedOut: false,
  };

  draggable.on('drag:start', () => {
    SoundFx.Wav.play('cubeUp');
    state.justLifted = true;
  });

  draggable.on('drag:stop', () => {
    if (!state.snappedIn) {
      SoundFx.Wav.play('cubeDown');
    }

    state.justLifted = false;
    state.snappedIn = false;
    state.snappedOut = false;
  });

  draggable.on('snap:in', () => {
    if (state.snappedIn) {
      return;
    }

    SoundFx.Wav.play('cubeDown');
    state.justLifted = false;
    state.snappedIn = true;
  });

  draggable.on('snap:out', () => {
    if (state.justLifted) {
      return;
    }

    SoundFx.Wav.play('cubeUp');
    state.snappedIn = false;
    state.snappedOut = true;
  });

  draggable.on('drag:over', (evt) => {
    if (evt.source.id === 'BasicsCube10' && evt.over.id === 'BasicsCube11') {
      evt.cancel();
    }

    if (evt.source.id === 'BasicsCube11' && evt.over.id === 'BasicsCube10') {
      evt.cancel();
    }
  });

  draggable.on('droppable:over', (evt) => {
    if (evt.dragEvent.source.id === 'BasicsCube10' && evt.droppable.id === 'BasicsCube3') {
      evt.cancel();
    }

    if (evt.dragEvent.source.id === 'BasicsCube11' && evt.droppable.id === 'BasicsCube7') {
      evt.cancel();
    }
  });

  return draggable;
}



