import Notification from '../Notification';

const swFilename = 'service-worker.js';

function register() {
  navigator.serviceWorker.register(swFilename).then((registration) => {
    return install(registration);
  }).catch((error) => {
    throw Error(`Error during service worker registration: ${error.message}`);
  });
}

function unregister() {
  navigator.serviceWorker.getRegistrations().then((registrations) => {
    for (let registration of registrations) {
      registration.unregister();
    }
  }).catch((error) => {
    throw Error(`Error unregistering service worker: ${error.message}`);
  });
}

function install(registration) {
  registration.onupdatefound = () => {
    const installingWorker = registration.installing;

    installingWorker.onstatechange = () => {
      switch (installingWorker.state) {
        case 'installed':
          if (navigator.serviceWorker.controller) {
            launchUpdateNotification();
          }
          break;

        case 'redundant':
          console.warn('The installing service worker became redundant.'); // eslint-disable-line no-console
          break;
      }
    };
  };
}

function launchUpdateNotification() {
  const notification = new Notification('Site updates are available. Tap to refresh.');

  document.body.appendChild(notification.getNotification());
}

export default {
  register,
  unregister,
};



