import fetchAudioBuffer from './helpers/fetch-audio-buffer';
import {supportsWebAudio} from '../SoundFx';

const initialGain = 0.001;
const maxGain = 0.6;
const duration = {
  up: 0.2,
  down: 0.3,
};
const timeout = (duration.down * 1000) + 10;

const assetPaths = {
  accessibleHover: 'assets/audio/accessible-hover.wav',
  accessibleActive: 'assets/audio/accessible-active.wav',
  extensible: 'assets/audio/extensible.wav',
};

const speeds = {
  default: 1.0,
  extensible: 1.5,
};

export default class Loop {
  static loopKeys = Object.keys(assetPaths);

  constructor(key) {
    if (!supportsWebAudio) {
      this.play = () => {
        return null;
      };

      return null;
    }

    if (!Loop.loopKeys.includes(key)) {
      return Error(`The requested sound is not available: ${key}`);
    }

    // Safari seems to have issues with creating the AudioContext right away...
    // will want to look into this when we do support Safari
    this.context = new AudioContext();

    this.label = key;
    this.gainNode = this.context.createGain();
    this.gainNode.gain.setValueAtTime(
      initialGain,
      this.context.currentTime
    );

    this.source = null;
    this.suspending = false;

    this.toggle = this.toggle.bind(this);
    this.play = this.play.bind(this);
    this.pause = this.pause.bind(this);
    this.increaseSpeed = this.increaseSpeed.bind(this);
    this.resetSpeed = this.resetSpeed.bind(this);

    fetchAudioBuffer(assetPaths[key], this.context).then((response) => {
      return this._startSource(response);
    }).catch(() => {
      return null;
    });
  }

  toggle() {
    if (this.context.state === 'running') {
      this._suspendSource();
    } else if (this.context.state === 'suspended') {
      this._resumeSource();
    }
  }

  play(muted = false) {
    this._resumeSource(muted);
  }

  pause() {
    this._suspendSource();
  }

  mute() {
    this.gainNode.gain.exponentialRampToValueAtTime(
      initialGain,
      this.context.currentTime + duration.up
    );
  }

  unmute() {
    this.gainNode.gain.exponentialRampToValueAtTime(
      maxGain,
      this.context.currentTime + duration.up
    );
  }

  increaseSpeed() {
    if (!speeds[this.label]) {
      throw Error(`The speed for ${this.label} does not exist!`);
    }

    this.source.playbackRate.exponentialRampToValueAtTime(
      speeds[this.label],
      this.context.currentTime + duration.up
    );
  }

  resetSpeed() {
    this.source.playbackRate.exponentialRampToValueAtTime(
      speeds.default,
      this.context.currentTime + duration.down
    );
  }

  _startSource(sound) {
    if (!sound) {
      return;
    }

    this.source = this.context.createBufferSource();
    this.source.buffer = sound;
    this.source.loop = true;
    this.source
      .connect(this.gainNode)
      .connect(this.context.destination);

    this.source.start();
    this.context.suspend();
  }

  _suspendSource() {
    if (this.suspending) {
      return;
    }

    this.suspending = true;

    this.gainNode.gain.exponentialRampToValueAtTime(
      initialGain,
      this.context.currentTime + duration.down
    );

    setTimeout(() => {
      this.context.suspend();
      this.suspending = false;
    }, timeout);
  }

  _resumeSource(muted) {
    if (this.suspending) {
      return;
    }

    this.context.resume().then(() => {
      return this.gainNode.gain.exponentialRampToValueAtTime(
        muted ? initialGain : maxGain,
        this.context.currentTime + duration.up
      );
    }).catch((error) => {
      return console.warn(`Something went wrong with resuming playback: ${error}`); // eslint-disable-line no-console
    });
  }
}



