import Drag from '../components/Drag';
import Stars from '../components/Stars';
import Features from '../components/Features';
import HeroPuzzle from '../components/Drag/Hero/HeroPuzzle';
import ServiceWorker from '../components/ServiceWorker';
import {supportsWebAudio} from '../components/SoundFx';

function initApp() {
  const Hero = new HeroPuzzle(document.getElementById('Hero'));
  Hero.init();

  Drag.Hero(HeroPuzzle, Hero);
  Drag.Basics();
  Drag.Swappable();
  Drag.Sortable();
  Drag.Collidable();

  if (supportsWebAudio) {
    Features.Accessible();
    Features.Extensible();
    Features.Interaction();
    Features.Animation();
  } else {
    const audioWarning = 'This browser does not support the latest Web Audio API. All sounds have been disabled.';
    console.warn(audioWarning); // eslint-disable-line no-console
  }
}

const is404 = document.getElementById('Error404');

if (is404) {
  is404.appendChild(Stars());
} else {
  initApp();
}

if ('serviceWorker' in navigator) {
  window.addEventListener('load', () => {
    ServiceWorker.register();
  });
}



