import SoundFx from '../../SoundFx';

export default () => {
  const target = document.getElementById('InteractionFeature');

  target.addEventListener('mouseenter', () => {
    SoundFx.Wav.play('interactionHover');
  });

  target.addEventListener('mouseleave', () => {
    SoundFx.Wav.play('interactionHover', true);
  });

  target.addEventListener('mousedown', () => {
    SoundFx.Wav.play('interactionActive');
  });

  target.addEventListener('mouseup', () => {
    SoundFx.Wav.play('interactionActive', true);
  });

  return target;
};



