import SoundFx from '../../SoundFx';

export default () => {
  const target = document.getElementById('AccessibleFeature');

  target.addEventListener('mouseenter', () => {
    SoundFx.AccessibleHover.play();
    SoundFx.AccessibleActive.play(true);
  });

  target.addEventListener('mouseleave', () => {
    SoundFx.AccessibleHover.pause();
    SoundFx.AccessibleActive.pause();
  });

  target.addEventListener('mousedown', () => {
    SoundFx.AccessibleActive.unmute();
  });

  target.addEventListener('mouseup', () => {
    SoundFx.AccessibleActive.mute();
  });

  return target;
};



