export const supportsWebAudio = ('AudioContext' in window);

const SoundFx = {
  Synth: new Synth(),
  Wav: new Wav(),
  AccessibleHover: new Loop('accessibleHover'),
  AccessibleActive: new Loop('accessibleActive'),
  Extensible: new Loop('extensible'),
};

export default SoundFx;



