import {Swappable} from '../../../scripts/vendor/draggable';
import SoundFx from '../../SoundFx';

export default function SwappableSection() {
  const containers = document.querySelectorAll('.CubesFrame--idSwappable .Cube');

  const draggable = new Swappable(containers, {
    draggable: '.draggable-source',
    appendTo: (source) => source.parentNode,
    classes: {
      body: 'draggable-container--is-dragging',
    },
  });

  let swappedNode = null;

  // --- Drag states --- //
  draggable.on('drag:start', () => {
    SoundFx.Synth.play('up');
  });

  draggable.on('swappable:swapped', ({swappedElement}) => {
    swappedNode = swappedElement;
    SoundFx.Synth.play('swap');
  });

  draggable.on('drag:stop', () => {
    if (swappedNode) {
      SoundFx.Synth.play('downGood');
    } else {
      SoundFx.Synth.play('downBad');
    }

    swappedNode = null;
  });

  return draggable;
}



