import {shuffleArray, shuffleArrayOnce} from '../../../../scripts/helpers/shuffle-array';
import {PuzzleData} from '../PuzzleData';

// Randomized faces per plane
// -----------------------------------------------------------------------------
function generateShuffledFaces(direction, easyMode = false) {
  const faces = [];

  PuzzleData.Cubes.forEach((cube) => {
    for (const face in cube) {
      if (cube.hasOwnProperty(face)) {
        if (face === direction) {
          faces.push(cube[face].face);
        }
      }
    }
  });

  // For debugging, generate the completed puzzle by returning `faces`
  return easyMode ? shuffleArrayOnce(faces) : shuffleArray(faces);
}

export const ShuffledFaces = {
  top: generateShuffledFaces('top', false),
  right: generateShuffledFaces('right', false),
  left: generateShuffledFaces('left', false),
};



