import {PuzzleData} from '../PuzzleData';
import {ShuffledFaces} from './ShuffledFaces';

export default function GenerateInitialState() {
  const state = [];

  PuzzleData.Cubes.forEach((cube, index) => {
    state.push({});

    for (const face in cube) {
      if (cube.hasOwnProperty(face)) {
        const randomId = ShuffledFaces[face].shift();
        const faceData = PuzzleData.Faces[face][randomId];

        state[index][face] = {
          [randomId]: faceData,
        };
      }
    }
  });

  return state;
}



